<template>
  <div class="container-fluid p-0">
    <div class="d-flex flex-column vh-100">
    <div class="bg-primary container col-12">
      <h1 class="text-light text-center py-3">{{title}}'s Todo List</h1>
    </div>
    <div class="d-flex flex-column my-3 row mt-5">
        
        <div class="col-10 offset-1 mb-2">
          <input type="text" v-model="newTask" class="form-control shadow-sm text-end">
          <small class="text-danger text-end d-block" v-if="validationStatus==true">Type something!!</small>
        </div>
        <div class="col-10 offset-1 mb-2">
          <button class="btn btn-success col-12" @keyup.enter="addTask()" @click="addTask()">Add Task</button>
        </div>
        
  
      
    </div>
    <div class=" col-lg-10 offset-lg-1 mt-4">
      <div class="row d-flex justify-content-center ">
      <div class="row mb-2">
        <div class="col ms-4 fs-4">Tasks</div>
        <div class="col-2 me-4 fs-4">Done</div>
      </div>
      <div class="d-flex justify-content-center py-3 rounded col-9 align-content-center" v-if="tasks.length==0">
        <h2 class="text-muted">No tasks yet!</h2>
      </div>
      <div class="row border" v-for="(task,index) in filteredTasks" :key="index">
        <div class="col ms-4" :class=" task.done ? 'delete' : '' ">{{task.action}}</div>
        <div class="col-2">
          <input type="checkbox" v-model="task.done" class="form-check-input">
        </div>
      </div>
      <div class="row mt-5">
        <div class="col-10 offset-1 bg-dark text-light rounded d-flex justify-content-between px-3 py-2 align-items-center"><h6>Hide done task</h6><input type="checkbox" v-model="hideCompleted"></div>
      </div>
      <div class="row mt-3">
        <div class="col-10 offset-1 bg-danger text-light rounded d-flex justify-content-center px-3 py-2 align-items-center" @click="clearTasks()"><h6>Clear all tasks</h6></div>
      </div>
      <div class="row mt-3">
        <div class="col-10 offset-1 bg-danger text-light rounded d-flex justify-content-center px-3 py-2 align-items-center" @click="clearDone()"><h6>Clear done tasks</h6></div>
      </div>
    </div>

    </div>
  </div>
  </div>
</template>

<script>
  export default {
    name: 'App',
    data: () => ({
      title: 'TYT',
      hideCompleted: false,
      validationStatus: false,
      newTask: '',
      tasks: [
        { 'action' : 'wake up early' , 'done' : true },
        { 'action' : 'take medicine' , 'done' : false},
        { 'action' : 'have a breakfast' , 'done' : true },
        { 'action' : 'coding' , 'done' : false},
      ]
    }),
    computed: {
      filteredTasks () {
        return this.hideCompleted ? this.tasks.filter(a=> !a.done) : this.tasks;
      }
    },
    methods: {
      addTask (){

          this.validation();
          this.tasks.push({
            'action' : this.newTask,
            'done' : false
          })
          this.newTask = ''
        
      },
      clearTasks(){
        this.tasks = [];
      },
      clearDone(){
        this.tasks = this.tasks.filter(a=> !a.done)
      },
      validation(){
        this.validationStatus = this.newTask == '' ? true : false ;
      }
    }
  }
</script>

<style>
  .delete{
    text-decoration: line-through;
  }
</style>
